* {box-sizing: border-box;}

body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
}

.topnav {
  overflow: hidden;
  background-color: #e9e9e9;
}

.topnav a {
  font-weight: bold;
  float: left;
  display: block;
  color: black;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
  color: #645e5e !important;
}

.topnav a:hover {
  background-color: #ddd;
  color: black;
}


.topnav .search-container {
  float: right;
  margin-right: 3%;
  
}

.topnav input[type=text] {
  padding: 6px;
  margin-top: 8px;
  font-size: 17px;
  border: none;
  padding-left: 30px;
}
.topnav input[type=text]:focus,.topnav input[type=text]:focus-visible{
  border:none;
  outline: none;
}

.topnav .search-container button {
  float: right;
  padding: 6px 10px;
  margin-top: 8px;
  margin-right: 16px;
  background: transparent;
    font-size: 17px;
    border: none;
    cursor: pointer;
    position: absolute;
}

.topnav .search-container button:hover {
  background: #ccc;
}

.fa {
  color: #8b8888;
}
@media screen and (max-width: 600px) {
  .topnav .search-container {
    float: none;
  }
  .topnav a, .topnav input[type=text], .topnav .search-container button {
    float: none;
    display: block;
    text-align: left;
    width: 100%;
    margin: 0;
    padding: 14px;

    
  }
  .topnav input[type=text] {
    border: 1px solid #ccc;  
  }
}

